[data-part="global-sidebar-container"] {
  h2,
  h3,
  h4,
  p,
  div {
    font-family: "Raleway";
  }
  h1 {
    font-family: "Berkshire Swash", handwriting;

    //hello
  }
}

svg {
  width: 100%;
}
.nav-link {
  svg {
    max-height: 1em;
  }
}
button,
a {
  svg {
    width: 1em;
    fill: currentColor;
  }
}

//box-shadow
.shadow {
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.shadow-md {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
}
.shadow-sm {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}
//--/--/--/--/--/--/--/--/--/--/--/--/--/--/--/--/--//
.admin-bar-space {
  @media (min-width: 650px) {
    margin-top: 46px;
  }
  @media (min-width: 783px) {
    margin-top: 32px;
  }
}
//

.button-group {
  display: flex;
  align-items: center;
  justify-content: space-between;
  button + button,
  a + a,
  button + a,
  a + button,
  a + .top-bar__button,
  .top-bar__button + a,
  button + .top-bar__button,
  .top-bar__button + button {
    margin-left: 10px;
  }
}

//ALL Search bars.
.search-bar {
  background: var(--input-bg);
  display: flex;
  align-items: center;
  width: 100%;

  border-radius: 8px;
  overflow: hidden;
}
.search-bar input[type="search"] {
  width: 100%;
  height: 50px;
  padding: 8px 40px 8px 16px;
  background-color: transparent;
  border: 1px solid transparent;
  &:active,
  &:focus {
    outline: 0;
    border: 1px solid transparent;
    background: var(--input-bg--hover);
  }
}
.search-bar button {
  color: #718096;
  display: flex;
  align-items: center;
  padding: 10px 10px 5px;
  justify-content: center;
  background-color: transparent;
  height: 100%;
  border-radius: 0;
  height: 50px;
  &:hover {
    background-color: var(--input-bg--hover);
  }
}

//Shared styles for Templates
.post-type-archive-recipe,
.tax-recipe_category,
.member_content-template-default {
  .ast-container {
    padding: 0 !important;
    max-width: 100% !important;
  }
  .custom-wrapper {
    padding: 0;
  }
}

%fancy-button {
  display: inline-flex;
  background-color: var(--theme-color);
  padding-left: 12px;
  padding-right: 12px;
  padding: 5px 16px;
  border-radius: 20px;
  position: relative;
  box-shadow: var(--shadow-md);
  color: white;
  &:hover {
    color: white;

    background-image: linear-gradient(
      135deg,
      rgba(0, 0, 0, 0.1) 0%,
      rgba(0, 0, 0, 0.1) 25%,
      transparent 25%,
      transparent 50%,
      rgba(0, 0, 0, 0.1) 50%,
      rgba(0, 0, 0, 0.1) 75%,
      transparent 75%,
      transparent
    );
    background-repeat: repeat-x;
    background-size: 3rem 3rem;
    animation: hover-animation 2s linear 0s infinite;
  }
  @keyframes hover-animation {
    from {
      background-position-x: 0%;
    }
    to {
      background-position-x: 150%;
    }
  }
}

a > strong {
  pointer-events: none !important;
}

