.member_content-single-layout {
  header {
    text-align: center;
    color: white;
    background-image: var(--header-bg);
    padding: 75px 0px 150px;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    &:after {
      content: "";
      position: absolute;
      inset: 0 0 0 0;
      background-color: rgba(black, 0.57);
      z-index: 0;
    }
    .wrapper {
      position: relative;
      z-index: 2;
    }
    .content_type {
      font-size: 1.54em;
      color: white;
      font-weight: bold;
      position: relative;
      display: inline-block;
      margin-bottom: 35px;
      &:hover {
        color: var(--theme-color);
      }
      &:after {
        content: "";
        width: 75%;
        height: 3px;
        background: white;
        position: absolute;
        bottom: -16px;
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }
  h2 {
    color: inherit;
    margin-bottom: 15px;
  }
  main {
    border-radius: 4px;
    box-shadow: var(--shadow-xl);
    position: relative;
    max-width: 860px;
    padding: 30px 20px;
    margin: 0 auto;
    margin-top: -160px;
    background: white;
    margin-bottom: 30px;
  }
  .downloads {
    padding: 10px;
    background: var(--input-bg);
    margin-top: 20px;
    border-radius: 3px;
    h3 {
      margin-top: 0;
      font-size: 1.5rem;
      margin-bottom: 5px;
    }
    a {
      display: inline-block;
      background: var(--blue-light);
      color: white;
      border-radius: 3px;
      padding: 8px 16px;
      &:hover {
        background: var(--blue-dark);
      }
    }
  }
  @media (min-width: 650px) {
    main {
      display: grid;
      grid-template-columns: 40% 1fr;
      gap: 40px;
    }
  }
}
